export default {
	data() {
		return {
			numReady:"10000",
			numCollection:"0",
			searchKey: [],
			ipt: '',
			searchClose: true,

		}
	},
	onPullDownRefresh:function(){
		console.log('refresh');
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onShow() {
		var vv = uni.getStorageSync('searchLocal');
		if(this.numReady>9999){
			console.log(11);
			this.numReady=this.numReady/10000+"万";
			console.log(this.numReady);
		}else{
			console.log(22);
		}
		console.log(vv);
		if(vv.length>0){
			var arr = vv.split("-");
			this.searchKey = arr;
		}
	},
	methods: {
		clearKey: function() {
			var that = this;
			uni.showModal({
				title: '提示',
				content: '点击确定将删除所有历史信息，确定删除吗？',
				success: function(res) {
					if (res.confirm) {
						that.searchKey = [];
						uni.setStorage({
							key: 'searchLocal',
							data: that.searchKey
						});
						
					} else if (res.cancel) {

					}
				}
			});

		},
		searchNow: function(e) {
			if (this.ipt == '') {
				uni.showToast({
					title: '未输入搜索关键字',
					icon: 'none',
					duration: 1000
				});
				return false;
			}
			var that = this;
			var newArr = that.searchKey;
			newArr.push(this.ipt);
			this.searchKey = newArr;
			var newStr = newArr.join('-');
			uni.setStorage({
				key: 'searchLocal',
				data: newStr
			});
		}
	}
}
